@import "https://fonts.googleapis.com/css2?family=Barlow:wght@400;500;600;700;800;900&family=Cairo:wght@400;600;700;800;900&family=Caveat:wght@400;500;600;700&family=League+Spartan:wght@400;500;600;700&family=M+PLUS+Rounded+1c:wght@400;500;700;800;900&family=Outfit:wght@400;700&family=Quicksand:wght@400;500&family=REM:wght@400;500;600;700&family=Varela+Round&display=swap";
.flex {
  justify-content: center;
  align-items: center;
  display: flex;
}

.flex-column {
  flex-direction: column;
  display: flex;
}

.grid-three-columns {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.grid-center {
  place-items: center;
  display: grid;
}

.right-border-radius {
  border-top-right-radius: 5rem;
  border-bottom-right-radius: 5rem;
}

.left-border-radius {
  border-top-left-radius: 5rem;
  border-bottom-left-radius: 5rem;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Barlow, sans-serif;
  font-size: 62.5%;
}

body {
  background-color: #afd3e2;
  width: 100vw;
  height: 100vh;
}

.container-app {
  width: 95%;
  height: 85%;
}

.section-left {
  background-color: #fff;
  width: 25%;
  height: 100%;
  padding: 2rem 3rem 3rem;
}

.search-bar {
  border: 2px solid #afd3e2;
  border-radius: 5rem;
  gap: .5rem;
  margin-bottom: 3rem;
  padding: .25rem 1rem;
  position: relative;
}

.search-bar > .search-button {
  background-color: #fff;
  border: none;
  border-radius: 1rem;
  padding: .5rem;
  transition: all .2s;
}

.search-bar > .search-button:active {
  transform: scale(.9);
}

.search-bar > .search-button:hover {
  cursor: pointer;
}

.search-button > .search-icon {
  width: 1.5rem;
}

.search-bar > input {
  opacity: .5;
  letter-spacing: 2px;
  border: none;
  flex-grow: 1;
  font-size: 1rem;
  font-weight: 700;
}

.search-bar > input:focus {
  opacity: 1;
  outline: none;
}

.error:after {
  content: "City Not Found! Please try again.";
  color: red;
  position: absolute;
  top: 50px;
}

.current-weather-icon {
  aspect-ratio: 1;
  align-self: center;
  width: 65%;
  margin-bottom: 1rem;
}

.current-weather-temp {
  align-self: flex-start;
  margin-bottom: 1.5rem;
  font-size: 6rem;
  font-weight: 600;
}

.current-temp-unit {
  font-size: 3rem;
}

.current-date {
  text-transform: uppercase;
  letter-spacing: 2px;
  border-bottom: 1px solid #afd3e2;
  gap: 1rem;
  width: 100%;
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
}

.current-date > .current-time-container {
  color: #20aad1cb;
  justify-content: space-between;
  max-width: 40%;
  font-size: 2rem;
  font-weight: 500;
  display: flex;
}

.current-forcast-container {
  gap: 2rem;
}

.current-forcast > .current-forcast-icon {
  width: 2.5rem;
}

.current-forcast {
  letter-spacing: 1px;
  color: #494949;
  align-self: flex-start;
  gap: 2rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.city-name {
  text-align: center;
  grid-column: 1 / -1;
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-size: 3rem;
}

.section-right {
  background-color: #dceaef;
  justify-content: center;
  width: 75%;
  height: 100%;
  padding: 3rem;
}

.section-control {
  justify-content: space-between;
  margin-bottom: 3rem;
}

.switch-weather-cards {
  text-transform: uppercase;
  color: #919191;
  gap: .5rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.switch-weather-cards > * {
  border-bottom: 1.5px solid #dceaef;
  padding-bottom: .25rem;
  transition: all .2s;
}

.switch-weather-cards > :hover {
  color: #000;
  cursor: pointer;
}

.active-weather-cards {
  color: #000;
  border-bottom: 1.5px solid #000;
}

.switch-temp-unit {
  gap: .5rem;
}

.switch-temp-unit > * {
  background-color: #fff;
  border: 1.5px solid #afd3e2;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 1.2rem;
  font-weight: 600;
  transition: all .2s;
}

.switch-temp-unit > :hover {
  color: #fff;
  cursor: pointer;
  background-color: #afd3e2;
  border-color: #fff;
}

.active-temp-unit {
  color: #fff;
  background-color: #afd3e2;
  border-color: #fff;
}

.weather-cards {
  justify-content: space-evenly;
  gap: .5rem;
  margin-bottom: 3rem;
}

.weather-card {
  background-color: #fff;
  border-radius: 2rem;
  align-items: center;
  gap: 1rem;
  width: 10vw;
  padding-block: 1rem;
  transition: all .2s;
  box-shadow: 4px 4px 0 2px #4fc4f61d;
}

.weather-card:hover {
  transform: translateY(-1rem);
}

.weather-card:active {
  transform: scale(1.5);
}

.weather-card > .weather-card-icon {
  width: 4rem;
}

.weather-card > .weather-card-date {
  font-size: 1.5rem;
  font-weight: 700;
}

.weather-card > .weather-card-temps {
  gap: 1rem;
  font-size: 2.5rem;
  font-weight: 600;
}

.highlights-heading {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
}

.highlights-container {
  align-self: center;
  gap: 2rem;
  width: 90%;
  height: 50%;
}

.highlight-card {
  background-color: #fff;
  border-radius: 2rem;
  align-items: center;
  gap: 2rem;
  transition: all .2s;
  box-shadow: 4px 4px 0 2px #4fc4f61d;
}

.highlight-card:hover {
  transform: translateY(-5px);
}

.highlight-card:active {
  transform: scale(1.2);
  box-shadow: 0 4px 0 4px #4fc4f61d;
}

.highlight-card-data {
  align-items: center;
  gap: 1rem;
}

.highlight-card-title {
  color: #afd3e2;
  font-size: 2rem;
  font-weight: 500;
}

.highlight-card-icon {
  width: 5rem;
}

.highlight-card-data-details > .data-value {
  font-size: 5rem;
  font-weight: 600;
}

.value-unit {
  font-size: 2rem;
  font-weight: 600;
}

.highlight-card-description {
  color: #789ba5c9;
  font-size: 1rem;
  font-weight: 500;
}

@media screen and (width <= 1000px) {
  html {
    font-size: 50%;
  }

  .container-app {
    height: 65vh;
  }
}

@media screen and (width <= 650px) {
  html {
    font-size: 40%;
  }

  .container-app {
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  .section-left {
    border-radius: 0;
