    row-gap: 1rem;
    width: 100%;
    height: 25%;
    padding: 1.5rem;
    display: grid;
  }

  .search-bar {
    grid-area: 1 / 2 / 2 / -1;
    align-self: center;
    width: 90%;
    margin: 0;
  }

  .current-weather-icon {
    grid-area: 1 / 1 / 2 / 2;
    justify-self: center;
    width: 50%;
    margin-bottom: 0;
    margin-right: 1.5rem;
  }

  .current-weather-temp {
    grid-area: 2 / 1 / 3 / 2;
    place-self: start center;
    margin-bottom: 0;
    font-size: 4.5rem;
  }

  .current-temp-unit {
    font-size: 2.5rem;
  }

  .current-date {
    border: 2.5px;
    border-right: 2.5px solid #afd3e2;
