  }

  .current-date > .current-time-container {
    align-self: flex-start;
  }

  .current-forcast-container {
    flex-direction: row;
    grid-area: 2 / 3 / 3 / -1;
    place-self: center start;
    gap: 2rem;
    margin-left: 2rem;
  }

  .current-forcast {
    gap: 1rem;
  }

  .section-right {
    border-radius: 0;
    width: 100%;
  }

  .switch-weather-cards {
    gap: 1.5rem;
    font-size: 2rem;
  }

  .switch-temp-unit > * {
    font-size: 2rem;
  }

  .weather-cards {
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    display: grid;
  }

  .weather-cards > * {
    gap: 2rem;
    width: 100%;
  }

  .weather-card-date, .weather-card-temps {
    transform: scale(1.5);
  }

  .highlights-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (width <= 400px) {
  .weather-cards {
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    display: grid;
  }
}

